    font-size: 14px;
    border-radius: 6px;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    background: var(--custom-white);
}

.radio-selected {
    border: 1px solid var(--custom-primary) !important;
    font-size: 14px;
    border-radius: 6px;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    background: var(--custom-white);
}

